<script>
import Vue from 'vue'
import VideoPlayer from 'vue-video-player'
import 'vue-video-player/src/custom-theme.css'
import 'video.js/dist/video-js.css'
Vue.use(VideoPlayer)
export default {
  props: ['webHomeParam'],
  data () {
    return {
      lang: 'zh',
      playerOptions: {
        autoplay: false, // true/false
        controls: true, // true/false
        width: 200, // 
        height: 200, // 
        loop: false, // :true/false
        muted: false, // true/false
        preload: 'metadata',
        fluid: false, // ,<video>vjs-fluidtrue
        inactivityTimeout: 0, // ,0
        language: 'en-US', // lang
        nativeControlsForTouch: false, // 
        sources: [{
          src: '' // videoUrl
        }],
        poster: '/static/img/hxbg.png',
        notSupportedMessage: '', // Video.js
        controlBar: {
          progressControl: true, // 
          volumePanel: false, // 
          timeDivider: false, // 
          durationDisplay: false, // 
          remainingTimeDisplay: false, //  
          fullscreenToggle: true // 
        }
      },
      playerOptions1: {
        autoplay: false, // true/false
        controls: true, // true/false
        width: 200, // 
        height: 200, // 
        loop: false, // :true/false
        muted: false, // true/false
        preload: 'metadata',
        fluid: false, // ,<video>vjs-fluidtrue
        inactivityTimeout: 0, // ,0
        language: 'en-US', // lang
        nativeControlsForTouch: false, // 
        sources: [{
          src: '' // videoUrl
        }],
        poster: '/static/img/video2-bg-zh.png',
        notSupportedMessage: '', // Video.js
        controlBar: {
          progressControl: true, // 
          volumePanel: false, // 
          timeDivider: false, // 
          durationDisplay: false, // 
          remainingTimeDisplay: false, //  
          fullscreenToggle: true // 
        }
      }
    }
  },
  watch: {
    webHomeParam: {
      deep: true,
      handler: function (newVal) {
        window.console.log('>>>>>' + this.webHomeParam.isZh)
        if (this.webHomeParam.isZh) {
          this.playerOptions.sources[0].src = 'https://hx-1254113200.cos.ap-shanghai.myqcloud.com/1-zh-small.mp4'
          this.playerOptions.poster = '/static/img/video1-bg-zh.png'
          this.playerOptions.language = 'zh-CN'
          this.playerOptions1.sources[0].src = 'https://hx-1254113200.cos.ap-shanghai.myqcloud.com/2-zh-hd.mp4'
          this.playerOptions1.poster = '/static/img/video2-bg-zh.png'
          this.playerOptions1.language = 'zh-CN'
        } else {
          this.playerOptions.sources[0].src = 'https://hx-1254113200.cos.ap-shanghai.myqcloud.com/1-en-hd.mp4'
          this.playerOptions.poster = '/static/img/video1-bg-en.png'
          this.playerOptions.language = 'en-US'
          this.playerOptions1.sources[0].src = 'https://hx-1254113200.cos.ap-shanghai.myqcloud.com/2-en-hd.mp4'
          this.playerOptions1.poster = '/static/img/video2-bg-en.png'
          this.playerOptions1.language = 'en-US'
        }
      }
    }
  },
  mounted () {
    this.lang = localStorage.lang || 'zh'
    this.playerOptions.notSupportedMessage = this.$t('home.videoin')
    this.lang === 'zh' ? this.playerOptions.sources[0].src = 'https://hx-1254113200.cos.ap-shanghai.myqcloud.com/1-zh-small.mp4' : this.playerOptions.sources[0].src = 'https://hx-1254113200.cos.ap-shanghai.myqcloud.com/1-en-hd.mp4'
    this.lang === 'zh' ? this.playerOptions.poster = '/static/img/video1-bg-zh.png' : this.playerOptions.poster = '/static/img/video1-bg-en.png'
    this.playerOptions1.notSupportedMessage = this.$t('home.videoin')
    this.lang === 'zh' ? this.playerOptions1.sources[0].src = 'https://hx-1254113200.cos.ap-shanghai.myqcloud.com/2-zh-hd.mp4' : this.playerOptions1.sources[0].src = 'https://hx-1254113200.cos.ap-shanghai.myqcloud.com/2-en-hd.mp4'
    this.lang === 'zh' ? this.playerOptions1.poster = '/static/img/video2-bg-zh.png' : this.playerOptions1.poster = '/static/img/video2-bg-en.png'
  },
  methods: {
    onPlayerPlay (player) {
      console.log('')
    }
  }
}
</script>

<style>
.videopage {
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  background-position: center center;
  background: url(/static/img/videobg.png);
}
.videopage .video_content {
  width: 1100px;
  margin: 0 auto;
}
.videopage .main5_img {
  width: 200px;
  height: 200px;
  margin-left: 500px;
  background: url(/static/img/main5-1.png);
  background-size: 100% 100%;
  background-position: center center;
}
.videopage .wordtitle {
  font-weight: bold;
  color: white;
  font-size: 30pt;
  padding-top: 120px;
}
.videopage .col_height {
  height: 210px;
}
.videopage .title1 {
  font-size:18px;
  font-family:NotoSansHans-Black;
  font-weight:900;
  color:rgba(255,255,255,1);
  text-align: left;
  padding-left: 33px;
}
.videopage .content {
  font-size:12px;
  font-family:MicrosoftYaHei;
  font-weight:400;
  color:rgba(255,255,255,1);
  text-align: left;
  padding-left: 33px;
}
.videopage .videoplay {
  width: 200px;
  height: 200px;
  margin: 0 auto;
}
</style>



